import { Button, Card, CardActions } from "@mui/material";
import GitHubIcon from "@mui/icons-material/GitHub";

const Project = ({ project }) => {
    return (
      <Card sx={{ maxWidth: 400 }} style={{cursor:"default", display:"flex",flexDirection:"column",justifyContent:"space-between"}} className="project">
        <div className='projImageContainer'>{project.hasImg ? <img src={project.img} className="projImage" alt={project.title}/> : <div/>}
        </div>
        <h3>{project.title}</h3>
        {/* <p style={{textAlign:"left", padding:"0px 10px 0px 10px"}}>{project.info}</p> */}
        {project.info.map(des => <p>{des}</p>)}
        <div>{project.info2.map((tag) => <Button variant="contained" style={{backgroundColor:"#d1b4ff", margin:"30px 10px 0 0", cursor:"default"}}>{tag}</Button>)}</div>
        <CardActions style={{paddingTop: "10px"}}>
        {project.hasRepo? <GitHubIcon size="large" cursor="pointer" onClick={() => window.open(project.repo)}>
        </GitHubIcon> : <div/>}
      </CardActions>
      </Card>
    );
  };
  
export default Project