import React, { useContext, useState, useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import profile from "../profile.js";
import Fade from 'react-reveal/Fade';

const About = () => {
    const [isDesktop, setIsDesktop] = useState(false);
    const [isMobile, setIsMobile] = useState(false);

    useEffect(() => {
        if (window.innerWidth > 769) {
          setIsDesktop(true);
          setIsMobile(false);
        } else {
          setIsMobile(true);
          setIsDesktop(false);
        }
      }, []);

    return (
        <section className='about'>
          <Container className='about-container'>
          <div className='about-wrapper'>
            <Fade bottom duration={1000} delay={600} distance="30px">
              <div className='about-wrapper__image'>
      
              </div>
              </Fade>
            <Fade left={isDesktop} bottom={isMobile} duration={1000} delay={1000} distance="30px">
            <div className='about-wrapper__info'>
          <p className='about-wrapper__info-text'>
            I recently graduated with a Bachelor of Science in Computer Science and Biology at the University of British Columbia.
            <br /> <br />
            I was previously at UBC Web Services for co-op, where I learned a lot about web development. I was also an Agile Developer Intern at SAP, where I worked on SAP Analytics Cloud and internal projects.
            <br /> <br />
            Originally a Biology major, I added CS onto my degree after discovering how much I enjoyed solving the kind of challenges I encountered in CS.
            <br /> <br />
            When I'm not coding, I enjoy drawing, and trying new coffeeshops + bakeries with friends!
            </p>
          </div>
          </Fade>
         
          </div>
          </Container>
        </section>
    );
  };
  
  export default About;
  